import React, { Component } from 'react';
import styles from './CrewGoal.scss';
import ellipse_1 from 'assets/img/semicircle_left.png'
import bg from 'assets/img/saefolder_bg.png'

class CrewGoal extends Component {
  state = {  }
  render() {
    return (
      <div className={styles.introduce}>
        <div className={styles.box}>
          <img src={ellipse_1} className={styles.ellipse_1} alt="ellipse_1" />
          <div className={styles.text_1}>
            
          </div>

          <div className={styles.text_2}>
                   <br/>
                .     35   <br/>
            8         .
          </div>

        </div>
        <img src={bg} className={styles.bg} alt="saefolder_bg" />
      </div>
    );
  }
}

export default CrewGoal;



